<template>
    <el-row :gutter="10" v-if="team">
        <el-row>
            <el-col :span="1">&nbsp;</el-col>

            <el-col :span="22">
                <h1>
                    {{ team.name }}

                    <small>
                        <el-button type="primary" @click="gotoChooseMember">
                            <i class="el-icon-plus"></i>

                            挑选队伍成员
                        </el-button>

                        <el-button type="danger" style="float: right; right: 10px;" @click="deleteDialogVisible = true">
                            <i class="el-icon-circle-close"></i>
                            删除队伍
                        </el-button>
                    </small>
                </h1>
            </el-col>

            <el-col :span="1">&nbsp;</el-col>
        </el-row>

        <el-dialog
            title="警告: 您将要将队伍删除"
            :visible.sync="deleteDialogVisible"
            size="tiny"
        >
            <span>
                确定要删除队伍 <em>{{ team.name }}</em> 吗?
            </span>

            <span slot="footer" class="dialog-footer">
                <el-button @click="deleteDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteTeam">确 定</el-button>
            </span>
        </el-dialog>

        <el-row>
            <el-col :span="1">&nbsp;</el-col>
            <el-col :span="21">
                <el-breadcrumb separator="/">
                    <el-breadcrumb-item :to="{ path: '/team' }">队伍列表</el-breadcrumb-item>
                    <el-breadcrumb-item>队伍管理</el-breadcrumb-item>
                </el-breadcrumb>
            </el-col>

            <el-col :span="2">
                &nbsp;
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="1">&nbsp;</el-col>
            <el-col :span="22">
                <el-tabs v-model="teamActiveName">
                    <el-tab-pane label="角色及位置" name="first">
                        <el-row>
                            <el-col :span="24">
                                <el-carousel v-if="team.members.length > 0" :autoplay="false" type="card" height="350px" @change="changeEditMember">
                                    <el-carousel-item v-for="(member, index) in team.members" :key="member.orgSkin.skinid" :label="member.orgSkin.card.cardname">
                                        <img :src="getImgSrc(member)" style="width: 100%;" @click="switchEditMember(index)" />
                                    </el-carousel-item>
                                </el-carousel>

                                <el-alert v-if="team.members.length == 0"
                                    title="目前队伍没有任何成员"
                                    type="info"
                                    description="请点击[挑选队伍成员]将人物加入队伍"
                                    show-icon
                                    :closable="false"
                                >
                                </el-alert>
                            </el-col>
                        </el-row>

                        <el-row :gutter="3" style="margin-bottom: 50px;">
                            <el-col :span="1">&nbsp;</el-col>

                            <el-col :span="22">
                                <el-tabs v-if="editMember" v-model="activeName">
                                    <el-tab-pane label="属性" name="first" style="min-height: 530px;">
                                        <member-simulate :edit-member="editMember"></member-simulate>

                                        <el-button type="success" @click="save">储存</el-button>

                                        <el-button @click="refreshEditMember">刷新</el-button>

                                        <el-button v-if="editMember" style="right: 50px; float:right;" type="danger" @click="removeOne">
                                            <i class="el-icon-circle-cross"></i>

                                            从队伍移除{{ editMember.orgSkin.card.cardname }}
                                        </el-button>
                                    </el-tab-pane>

                                    <el-tab-pane label="符卡" name="second" style="min-height: 530px;">
                                        <member-spell-card-intro :skin="editMember.orgSkin"></member-spell-card-intro>
                                    </el-tab-pane>

                                    <el-tab-pane label="成员比较" name="third" style="min-height: 530px;">
                                        <el-select v-model="cpSkinIds" multiple placeholder="请选择">
                                            <el-option v-for="member in team.members"
                                                :key="member.orgSkin.skinid"
                                                :label="member.orgSkin.card.cardname"
                                                :value="member.orgSkin.skinid"
                                            >
                                            </el-option>
                                        </el-select>

                                        <br>
                                        <br>

                                        <el-table
                                            :data="getCpMembers"
                                            border
                                            style="width: 100%"
                                        >
                                            <el-table-column
                                            fixed
                                            label="人物"
                                            width="180">
                                                <template scope="scope">
                                                    <el-button size="tiny" v-if="editMember.orgSkin.skinid != scope.row.skinid" @click="removeFromCp(scope.row.skinid)">
                                                        <i class="el-icon-circle-cross"></i>
                                                    </el-button>

                                                    {{ scope.row.name }}
                                                </template>
                                            </el-table-column>

                                            <el-table-column
                                            sortable
                                            label="等级"
                                            prop="level"
                                            width="100"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="信仰"
                                            prop="use_faith"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="饭团"
                                            prop="use_food"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            label="类型"
                                            prop="atktype"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="力量"
                                            prop="atk_mel"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="灵力"
                                            prop="atk_rang"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="生命"
                                            prop="hp"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="防御"
                                            prop="def"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="回避"
                                            prop="avoid"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="命中"
                                            prop="hitrate"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="暴击"
                                            prop="crit"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            sortable
                                            label="幸运"
                                            prop="lucky"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            :sortable="true"
                                            label="格档"
                                            prop="block"
                                            width="120"></el-table-column>

                                            <el-table-column
                                            label="攻击符卡"
                                            prop="atkSpell"
                                            width="200"></el-table-column>

                                            <el-table-column
                                            label="防御符卡"
                                            prop="defSpell"
                                            width="200"></el-table-column>

                                            <el-table-column
                                            label="支援符卡"
                                            prop="supSpell"
                                            width="200"></el-table-column>
                                        </el-table>
                                    </el-tab-pane>

                                    <el-tab-pane label="出阵位置" name="four" style="min-height: 530px;">
                                        <el-row :gutter="2">
                                            <el-col :xs="24" :sm="11" :md="12" :lg="12">
                                                <h4>
                                                    <small>
                                                        <el-alert v-show="!editMember.position"
                                                            :title="getAddToPosition"
                                                            type="info"
                                                            show-icon
                                                            :closable="false"
                                                        ></el-alert>

                                                        <el-button v-show="editMember.position" type="default" @click="clearOne">
                                                            <i class="el-icon-star-off"></i>

                                                            将{{ editMember.orgSkin.card.cardname }}移出先发
                                                        </el-button>
                                                    </small>
                                                </h4>

                                                <el-tooltip class="item" effect="dark" :content="getSkinDialog" placement="top-start">
                                                    <img :src="getImgSrc(editMember)" style="max-width: 380px; position: relative;">
                                                </el-tooltip>
                                            </el-col>

                                            <el-col :xs="24" :sm="13" :md="12" :lg="12">
                                                <el-row>
                                                    <el-col :span="16">
                                                        <team-cost :team="team"></team-cost>
                                                    </el-col>

                                                    <el-col :span="8">
                                                        <el-button type="warning" @click="clearAll">
                                                            <i class="el-icon-star-off"></i>
                                                            清空先发
                                                        </el-button>
                                                    </el-col>
                                                </el-row>

                                                <team-quota-position v-if="team" :edit-member="editMember" :team="team" :is-draggable="false"
                                                    @click="quotaClickHandle"
                                                ></team-quota-position>
                                            </el-col>
                                        </el-row>
                                    </el-tab-pane>
                                </el-tabs>
                            </el-col>

                            <el-col :span="1">&nbsp;</el-col>
                        </el-row>
                    </el-tab-pane>

                    <el-tab-pane label="队伍资讯" name="second">
                        <el-form ref="form" :model="form" label-width="80px">
                            <el-form-item label="队伍名称">
                                <el-input v-model="team.name"></el-input>
                            </el-form-item>

                            <el-form-item label="队伍说明">
                                <el-input type="textarea" v-model="team.note"></el-input>
                            </el-form-item>

                            <el-form-item label="适用地图">
                                <el-tree
                                    :data="getMapGroups"
                                    show-checkbox
                                    node-key="name"
                                    ref="tree"
                                    highlight-current
                                    :props="defaultProps"
                                >
                                </el-tree>
                            </el-form-item>

                            <el-form-item>
                                <el-button type="primary" @click="saveTeamInfo">储存</el-button>
                                <el-button @click="reverseTeamInfo">还原</el-button>
                            </el-form-item>
                        </el-form>
                    </el-tab-pane>
                </el-tabs>
            </el-col>
            <el-col :span="1">&nbsp;</el-col>
        </el-row>
    </el-row>
</template>

<script>

import { mapGetters, mapActions } from 'vuex'

import TeamService from '../../services/TeamService'
import SkinService from '../../services/SkinService'

import TeamCost from './Widget/TeamCost'
import TeamQuotaPosition from './Partials/TeamQuotaPosition'
import MemberSimulate from './Partials/MemberSimulate'
import MemberSpellCardIntro from './Partials/MemberSpellCardIntro'

export default {
    props: [],

    data() {
        return {
            activeName: 'first',
            teamActiveName: 'first',
            editMember: null,
            team: null,
            isPositionLock: false,
            deleteDialogVisible: false,
            form: {},
            defaultProps: {
                children: 'children',
                label: 'label'
            },
            cpSkinIds: [],
        }
    },

    methods: Object.assign(
        mapActions([
            'INIT_TEAMS',
            'UPDATE_TEAM',
            'DESTROY_TEAM',
        ]),

        {
            getMemberDisplayAttrVal(member, attrName, weapons, armors) {
                return TeamService.getDisplayAttrVal(member, attrName, { weapons: this.getWeapons, armors: this.getArmors })
            },

            getWeapon(equipmentid) {
                return _.find(this.getWeapons, { equipmentid })
            },

            getArmor(equipmentid) {
                return _.find(this.getArmors, { equipmentid })
            },

            getAtkType(val) {
                return SkinService.getAtkType(val)
            },

            saveTeamInfo() {
                this.team.maps = this.getCheckedKeys()

                this.UPDATE_TEAM(this.team)

                this.$message({
                    showClose: true,
                    message: `队伍资讯更新完成!`,
                    type: 'success'
                })
            },

            reverseTeamInfo() {
                let index = _.findIndex(this.getTeams, { id: this.team.id })

                this.INIT_TEAMS(true)

                this.team = null
                this.team = this.getTeams[index]
                this.setCheckedKeys(this.team.maps)
            },

            getCheckedKeys() {
                let maps = []

                _.forEach(this.$refs.tree.getCheckedKeys(), map => {
                    let mapArr = map.split('-')

                    if (_.isEqual(3, mapArr.length)) {
                        maps.push(map)
                    }
                })

                return maps
            },

            setCheckedKeys(maps) {
                this.$refs.tree.setCheckedKeys(maps)
            },

            deleteTeam() {
                this.DESTROY_TEAM(this.team)

                this.returnToTeamList()
            },

            returnToTeamList() {
                this.$router.push({ name: 'team-list' })
            },

            changeEditMember(index) {
                this.editMember = this.team.members[index]
            },

            gotoChooseMember() {
                let address = {
                    name: 'team-member-choose',
                    params: { id: this.$route.params.id },
                }

                this.$router.push(address)
            },

            switchEditMember(index) {
                this.editMember = this.team.members[index]
            },

            getImgSrc(member) {
                return require(`../../assets/char/${member.orgSkin.skinid}.png`)
            },

            save() {
                this.UPDATE_TEAM(this.team)

                this.$message({
                    message: `${this.editMember.orgSkin.card.cardname}加点资讯储存完成!`,
                    type: 'success',
                })
            },

            refreshEditMember() {
                let index = _.findIndex(this.team.members, function (member) {
                    return _.isEqual(this.editMember.orgSkin.skinid, member.orgSkin.skinid)
                }.bind(this))

                this.INIT_TEAMS(true)

                this.init()

                this.editMember = this.team.members[index]
            },

            /**
             * 不论几个 Null, 皆视为 switch 处理
             *
             * @param  {String} desc
             * @return {Void}
             */
            quotaClickHandle(desc) {
                if (_.isEqual(true, this.isPositionLock)) {
                    return false
                }

                this.isPositionLock = true
                let src = this.editMember.position
                let descMember = _.find(this.team.members, { position: desc })

                this.editMember.position = desc

                // 这段冗余 code 用来防止 css 位置错误用的
                this.team[desc] = null
                this.team = _.assign({}, this.team)

                setTimeout(function () {
                    this.team[desc] = this.editMember

                    setTimeout(function () {
                        // switch 时会发生位置错误, 而和不 switch 差异就是 descMember 是否为空这点
                        if (!_.isNil(descMember)) {
                            descMember.position = src
                        }

                        if (!_.isNil(src)) {
                            this.team[src] = descMember
                        }

                        this.team = _.assign({}, this.team)
                        this.isPositionLock = false
                        this.savePosition()
                    }.bind(this), 200)
                }.bind(this), 100)
            },

            savePosition(positions) {
                this.UPDATE_TEAM(this.team)

                this.$message({
                    message: `队伍位置更新完成!`,
                    type: 'success',
                })
            },

            saveMembers() {
                this.UPDATE_TEAM(this.team)

                this.$message({
                    message: `轮替阵容更新完成!`,
                    type: 'success',
                })
            },

            removeOne() {
                this.clearOne()

                let index = _.findIndex(this.team.members, function (member) {
                    return _.isEqual(member.orgSkin.skinid, this.editMember.orgSkin.skinid)
                }.bind(this))

                this.team.members.splice(index, 1)
                this.team = _.assign({}, this.team)
                this.editMember = _.isEmpty(this.team.members) ? null : this.team.members[0]

                this.saveMembers()
            },

            clearOne() {
                let position = this.editMember.position

                this.team[position] = null
                this.editMember.position = null
                this.team = _.assign({}, this.team)

                this.savePosition()
            },

            clearAll() {
                _.forEach(this.getPositions, function (positionName) {
                    this.team[positionName] = null
                }.bind(this))

                _.forEach(this.team.members, function (member) {
                    member.position = null
                }.bind(this))

                this.savePosition()
            },

            init() {
                this.team = _.assign({}, this.teamByParams)

                _.forEach(this.getPositions, function (pos) {
                    if (_.isNil(this[pos])) {
                        this.pos = null
                    }
                }.bind(this))

                this.team = _.assign({}, this.team)

                if (!_.isEmpty(this.team.members)) {
                    this.editMember = this.team.members[0]
                }

                Vue.nextTick(function () {
                    // You should setTreeKey here, because you should wait DOM rendered completed
                    this.setCheckedKeys(this.team.maps)
                }.bind(this))
            },

            removeFromCp(skinid) {
                let index = _.findIndex(this.cpSkinIds, function (cpSkinId) {
                    return _.isEqual(skinid, cpSkinId)
                }.bind(this))

                this.cpSkinIds.splice(index, 1)
            },
        },
    ),

    computed: Object.assign(
        mapGetters([
            'getTeams',
            'getMapGroups',
            'getPositions',
            'getWeapons',
            'getArmors',
        ]),

        {
            getAddToPosition() {
                return `点击右侧空格可将${this.editMember.orgSkin.card.cardname}加入先发`
            },

            teamByParams() {
                let teamId = this.$route.params.id

                return _.find(this.getTeams, { id: teamId })
            },

            getSkinDialog() {
                return SkinService.getSkinDialog(this.editMember.orgSkin)
            },

            getCpMembers() {
                let pool = []
                let cpSkinIds = []

                _.remove(this.cpSkinIds, function (skinId) {
                    return _.isEqual(skinId, this.editMember.orgSkin.skinid)
                }.bind(this))

                cpSkinIds = JSON.parse(JSON.stringify(this.cpSkinIds))
                cpSkinIds.unshift(this.editMember.orgSkin.skinid)

                _.forEach(cpSkinIds, function (skinid) {
                    let member = _.find(this.team.members, function (eachMember) {
                        return _.isEqual(eachMember.orgSkin.skinid, skinid)
                    }.bind(this))

                    let adaptedMember = {
                        name: member.orgSkin.card.cardname,
                        level: member.level,
                        skinid:  member.orgSkin.skinid,
                        atktype: this.getAtkType(member.orgSkin.atktype),
                        use_faith: member.orgSkin.use_faith,
                        use_food: member.orgSkin.use_food,
                        atk_mel: this.getMemberDisplayAttrVal(member, 'atk_mel', this.getWeapons, this.getArmors),
                        atk_rang: this.getMemberDisplayAttrVal(member, 'atk_rang', this.getWeapons, this.getArmors),
                        hp: this.getMemberDisplayAttrVal(member, 'hp', this.getWeapons, this.getArmors),
                        hitrate: this.getMemberDisplayAttrVal(member, 'hitrate', this.getWeapons, this.getArmors),
                        def: this.getMemberDisplayAttrVal(member, 'def', this.getWeapons, this.getArmors),
                        avoid: this.getMemberDisplayAttrVal(member, 'avoid', this.getWeapons, this.getArmors),
                        crit: this.getMemberDisplayAttrVal(member, 'crit', this.getWeapons, this.getArmors),
                        lucky: this.getMemberDisplayAttrVal(member, 'lucky', this.getWeapons, this.getArmors),
                        block: this.getMemberDisplayAttrVal(member, 'block', this.getWeapons, this.getArmors),
                        atkSpell: _.isNil(member.orgSkin.atkSpell) ? null : member.orgSkin.atkSpell.content,
                        defSpell: _.isNil(member.orgSkin.defSpell) ? null : member.orgSkin.defSpell.content,
                        supSpell: _.isNil( member.orgSkin.supSpell) ? null : member.orgSkin.supSpell.content,
                    }

                    pool.push(adaptedMember)
                }.bind(this))

                return pool
            },
        },
    ),

    components: {
        TeamCost,
        TeamQuotaPosition,
        MemberSimulate,
        MemberSpellCardIntro,
    },

    mounted() {
        this.INIT_TEAMS()

        this.init()
    },
}
</script>


