<template>

    <el-card class="box-card" style="margin-right: 5px;">
        <div slot="header" :style="getCardStyle()" @click="clickHandle">

            <i @click.stop="addMember" v-show="!inTeam" class="el-icon-star-off"></i>
            <i @click.stop="removeMember" v-show="inTeam" class="el-icon-star-on"></i>

            <span style="line-height: 36px; float:right;" v-once>
                {{ skin.card.cardname }} - {{ skin.skinname }}
            </span>
        </div>

        <el-row :gutter="3">
            <member-attr-board :skin="skin" font-type="small"></member-attr-board>
        </el-row>
    </el-card>

</template>

<script>

import SkinService from '../../../services/SkinService'
import MemberAttrBoard from './MemberAttrBoard'

export default {
    props: ['skin', 'inTeam'],

    methods: {
        addMember() {
            return this.$emit('add', this.skin)
        },

        removeMember() {
            return this.$emit('remove', this.skin)
        },

        getSkinAttrValue(skin, attrName) {
            return SkinService.getSkinAttrValue(skin, attrName)
        },

        getAtkType(val) {
            return SkinService.getAtkType(val)
        },

        getSpecialAttrBonus(skin, attrName) {
            return SkinService.getSpecialAttrBonus(skin, attrName)
        },

        displayCostJade(num) {
            return SkinService.displayCostJade(num)
        },

        getSpecialAttrBonusStyle(skin, attrName) {
            let bonus = this.getSpecialAttrBonus(skin, attrName)

            if (_.isEqual(0, bonus)) {
                return {
                    display: 'none',
                }
            }

            let color = _.lt(0, bonus) ? '#13CE66' : '#FF4949';

            return {
                color,
                float: 'right',
                'padding-right': '16px',
                'font-weight': 'bold',
            }
        },

        getCardStyle() {
            let url = require(`../../../assets/char/${this.skin.skinid}.png`)

            return {
                'background-image': `url('${url}')`,
                height: '150px',
                'background-size': '75%',
                'background-repeat': 'no-repeat',
                cursor: 'pointer',
            }
        },

        clickHandle() {
            return this.$emit('click', this.skin)
        },
    },

    components: {
        MemberAttrBoard,
    },
}
</script>


