<template>
    <div>
        <el-row>
            <el-col :span="24" style="background-color: #F9FAFC; padding: 5px; margin-bottom: 10px;">
                <h4>
                    @暴击  <small>(k = 75)</small>
                </h4>

                 <el-row>
                    <el-col :span="8">
                         <el-alert show-icon type="success" style="margin: 3px 5px 10px 5px;" :closable="false" title="参考@xformxp大分享之数据" description="原文连结: https://tieba.baidu.com/p/4977122054">
                        </el-alert>
                    </el-col>

                    <el-col :span="16">&nbsp;</el-col>
                </el-row>

                <label>暴击:</label>
                <el-input-number v-model="crit" :min="0"></el-input-number>

                <label>幸运:</label>
                <el-input-number v-model="lucky" :min="0"></el-input-number>

                <label>防御:</label>
                <el-input-number v-model="def" :min="0"></el-input-number>

                <br/>
                <br/>
                <b>暴击机率:</b> <el-tag type="danger">{{ critPercentage }}</el-tag>
                <b>暴击伤害:</b> <el-tag type="warning">{{ critDamage }}</el-tag>
                <b>防御减免:</b> <el-tag >{{ defDiscount }}</el-tag>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="24" style="background-color: #F9FAFC; padding: 5px; margin-bottom: 10px;">
                <h4>@格档 <small>(k = 90)</small></h4>

                <el-row>
                    <el-col :span="8">
                         <el-alert show-icon type="success" style="margin: 3px 5px 10px 5px;" :closable="false" title="参考@xformxp大分享之数据" description="原文连结: https://tieba.baidu.com/p/4977122054">
                        </el-alert>
                    </el-col>

                    <el-col :span="16">&nbsp;</el-col>
                </el-row>

                <label>格档:</label>
                <el-input-number v-model="block" :min="0"></el-input-number>

                <br/>
                <br/>
                <b>格档率:</b>
                <el-tag type="info">{{ blockPercentage }}</el-tag>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="24" style="background-color: #F9FAFC; padding: 5px; margin-bottom: 10px;">
                <h4>
                    @命中
                </h4>

                <el-row>
                    <el-col :span="8">
                        <el-alert show-icon type="warning" style="margin: 3px 5px 10px 5px;" title="不考虑体术加成和等级压制" description="命中系依据个人臆测公式计算，结果仅供参考">
                        </el-alert>
                    </el-col>

                    <el-col :span="16">&nbsp;</el-col>
                </el-row>


                <label>命中:</label>
                <el-input-number v-model="hitrate" :min="0"></el-input-number>

                <label>闪避:</label>
                <el-input-number v-model="avoid" :min="0"></el-input-number>

                <br/>
                <br/>
                <b>命中率:</b> <el-tag>{{ hitratePercentage }}</el-tag>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            hitrate: 0,
            avoid: 0,
            lucky: 0,
            crit: 0,
            def: 0,
            block: 0,
        }
    },

    computed: {
        hitratePercentage() {
            if (_.isEqual(0, this.hitrate)) {
                return '0%'
            }

            // Y=2/（2+X）
            let x = this.avoid/this.hitrate
            let y = 2/(2 + x)

            return `${Math.round(y * 100)}%`
        },

        critPercentage() {
            let per = (this.crit - 0.5 * this.lucky)/ (this.crit + 75)

            return `${Math.round(per * 100)}%`
        },

        critDamage() {
            return Math.round((this.crit - this.lucky) / (1 + this.def/120))
        },

        blockPercentage() {
            return `${Math.round(100 * this.block/(this.block + 80))}%`
        },

        defDiscount() {
            return `${Math.round(100 * this.def/(this.def + 120))}%`
        },
    },
}
</script>


