import Vue from 'vue'
import Router from 'vue-router'
import TeamList from '@/components/Team/List'
import TeamShow from '@/components/Team/Show'
import TeamMemberChoose from '@/components/Team/MemberChoose'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            redirect: '/team',
        },
        {
            path: '/team',
            name: 'team-list',
            component: TeamList,
        },

        {
            path: '/team/:id',
            name: 'team-show',
            component: TeamShow,
        },

        {
            path: '/team/:id/team-member-choose',
            name: 'team-member-choose',
            component: TeamMemberChoose,
        }
    ],

    // mode: 'history',
})



