<template>
    <div>
        <el-popover
            ref="popover1"
            placement="left"
            width="300"
            trigger="hover"
            :offset="-130"
        >
            <p class="text">
                {{ getSkinDialog }}
            </p>
        </el-popover>

        <img :src="getTargetImgSrc" v-popover:popover1 :style="getImgMaxWidth">
    </div>
</template>

<script>

import SkinService from '../../../services/SkinService'

export default {
    props: ['skin', 'span'],

    computed: {
        getSkinDialog() {
           return SkinService.getSkinDialog(this.skin)
        },

        getTargetImgSrc() {
            return require(`../../../assets/char/${this.skin.skinid}.png`)
        },

        getImgMaxWidth() {
            return {
                'max-width': `${this.span * 50}px`,
            }
        },
    },
}
</script>


