<template>
    <el-row :gutter="5" v-if="team">
        <el-row>
            <el-col :span="1">&nbsp;</el-col>
            <el-col :span="23">
                <h3>
                    挑选 <em>{{ team.name }}</em> 成员

                    <small>
                        <el-button type="primary" @click="dialogVisible = true">
                            <i class="el-icon-view"></i>

                            进阶搜寻
                        </el-button>
                    </small>
                </h3>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="1">&nbsp;</el-col>
            <el-col :span="23">
                <el-breadcrumb separator="/">
                    <el-breadcrumb-item :to="{ path: '/team' }">队伍列表</el-breadcrumb-item>
                    <el-breadcrumb-item :to="{ path: '/team/' + $route.params.id }">队伍管理</el-breadcrumb-item>
                    <el-breadcrumb-item>队伍新增成员</el-breadcrumb-item>
                </el-breadcrumb>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="1">&nbsp;</el-col>
            <el-col :span="23">
                <el-radio-group v-model="selectedChapter" @change="changeChapter">
                    <el-radio-button label="全部"></el-radio-button>
                    <el-radio-button label="其他"></el-radio-button>
                    <el-radio-button v-for="chapter in chapters" :key="chapter" :label="getChapterName(chapter)"></el-radio-button>
                </el-radio-group>
            </el-col>
        </el-row>

       <el-col :span="24">
            <el-pagination
                :current-page="pagination.start"
                :page-size="pagination.perPage"
                layout="prev, pager, next, total"
                :total="skins.length"
                @current-change="currentChangeHandle"
                style="margin: 30px 30px;"
            >
            </el-pagination>
        </el-col>

        <el-col :xs="24" :sm="12" :md="12" :lg="8" v-for="skin in getMappedSkins" :key="skin.skinid">
            <member-card :skin="skin" :in-team="inTeam(skin)" @click="openChampionSkinDialog" @add="addMember" @remove="removeMember"></member-card>
        </el-col>

        <el-col :span="24">
            <el-pagination
                :current-page="pagination.start"
                :page-size="pagination.perPage"
                layout="prev, pager, next, total"
                :total="skins.length"
                @current-change="currentChangeHandle"
                style="margin: 30px 30px;"
            >
            </el-pagination>
        </el-col>

        <el-dialog
            title="请选择搜寻条件"
            :visible.sync="dialogVisible"
            size="full"
        >
            <el-form ref="form" label-width="80px">
               <el-form-item label="符卡玉耗">
                    <el-select v-model="jade.type" clearable placeholder="请选择符卡种类">
                        <el-option label="攻击符卡" value="atkSpell"></el-option>
                        <el-option label="防御符卡" value="defSpell"></el-option>
                        <el-option label="支援符卡" value="supSpell"></el-option>
                    </el-select>

                    <el-slider
                        v-show="jade.type && jade.type.length > 0"
                        v-model="jade.cost"
                        range
                        show-stops
                        :min="0"
                        :max="5"
                    ></el-slider>
                </el-form-item>

                <el-form-item label="出击消耗">
                    <el-select v-model="resource.type" clearable placeholder="请选择消耗种类">
                        <el-option label="信仰" :value="1"></el-option>
                        <el-option label="饭团" :value="2"></el-option>
                        <el-option label="信仰 + 饭团" :value="3"></el-option>
                    </el-select>

                    <el-slider
                        v-show="resource.type > 0"
                        v-model="resource.cost"
                        range
                        show-stops
                        :min="1"
                        :max="30"
                    ></el-slider>
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="resetSearchConditions">
                    <i class="el-icon-circle-close"></i>
                    清空
                </el-button>
                <el-button type="success" @click="setSearchConditions">
                    <i class="el-icon-search"></i>
                    确定
                </el-button>
            </span>
        </el-dialog>

        <el-dialog v-if="targetSkin" :title="targetSkin.cardname" :visible.sync="dialogTableVisible" size="large">
            <el-row :gutter="5">
                <member-head-title :skin="targetSkin" @to-prev="switchToPrevSkin" @to-next="switchToNextSkin"></member-head-title>

                <el-col :md="12" :sm="24" :xs="24">
                    <member-spell-card-intro :skin="targetSkin"></member-spell-card-intro>
                </el-col>

                <el-col :md="12" :sm="24" :xs="24">
                    <member-attr-board :skin="targetSkin"></member-attr-board>
                </el-col>
            </el-row>

            <el-row :gutter="20">

                <el-col :span="12" :sm="24" :xs="24">
                    <member-image :skin="targetSkin" :span="10"></member-image>
                </el-col>

                <el-col :span="12" :sm="24" :xs="24">
                    <h4>队伍轮替名单</h4>

                    <el-button type="success" @click="addToTeamMembers">加入{{ this.targetSkin.card.cardname }} </el-button>

                    <br>

                    <el-tag
                        v-for="member in team.members"
                        :key="member.orgSkin.card.cardid"
                        :closable="!inStarting(member.orgSkin)"
                        :type="getTagType(member.orgSkin)"
                        @close="removeMemberFromTeam(member)"
                        style="margin: 5px;"
                    >
                        {{ member.orgSkin.card.cardname }}
                    </el-tag>
                </el-col>
            </el-row>
        </el-dialog>

    </el-row>

</template>

<script>

import { mapGetters, mapActions } from 'vuex'

import MockSkinService from '../../services/MockSkinService'
import SkinService from '../../services/SkinService'
import TeamService from '../../services/TeamService'
import MemberAttrBoard from './Partials/MemberAttrBoard'
import MemberCard from './Partials/MemberCard'
import MemberSpellCardIntro from './Partials/MemberSpellCardIntro'
import MemberHeadTitle from './Partials/MemberHeadTitle'
import MemberImage from './Partials/MemberImage'

export default {
    data() {
        return {
            dialogVisible: false,
            dialogTableVisible: false,
            radio: 0,
            chapter: '',
            pagination: {
                start: 1,
                perPage: 30,
            },
            targetSkin: null,
            chapterMap: SkinService.chapterMap,
            jade: _.assign({}, SkinService.orgConditions).jade,
            resource: _.assign({}, SkinService.orgConditions).resource,
            selectedChapter: '全部',
        }
    },

    methods: Object.assign(
        mapActions([
            'INIT_TEAMS',
            'ADD_TEAM_MEMBER',
            'REMOVE_MEMBER_FROM_TEAM',
            'SET_SEARCH_CONDITIONS',
        ]),

        {
            changeChapter(val) {
                this.selectedChapter = val

                this.syncStore()
            },

            syncStore() {
                this.SET_SEARCH_CONDITIONS({
                    jade: this.jade,
                    resource: this.resource,
                    chapter: this.selectedChapter,
                })
            },

            getChapterName(chapter) {
                return chapter.replace('东方', '')
            },

            setSearchConditions() {
                this.syncStore()

                this.dialogVisible = false
            },

            resetSearchConditions() {
                this.jade = _.assign({}, SkinService.orgConditions)
                this.resource = _.assign({}, SkinService.orgConditions)

                this.syncStore()
            },

            inTeam(skin) {
                if (_.isNil(skin)) {
                    return false
                }

                let inMember = _.find(this.team.members, function (member) {
                    if (_.isNil(member.orgSkin)) {
                        return false
                    }

                    return _.isEqual(member.orgSkin.skinid, skin.skinid)
                }.bind(this))

                return !_.isNil(inMember)
            },

            inStarting(skin) {
                if (_.isNil(skin)) {
                    return false
                }

                let pos = _.find(TeamService.positions, function (posName) {
                    return !_.isNil(this.team[posName]) && _.isEqual(this.team[posName].orgSkin.skinid, skin.skinid)
                }.bind(this))

                return !_.isNil(pos)
            },

            getTagType(skin) {
                return this.inStarting(skin) ? 'primary' : 'default' 
            },

            addMember(skin) {
                this.ADD_TEAM_MEMBER({ teamId: this.team.id, mockSkin: skin })
            },

            removeMember(skin) {
                let member = _.find(this.team.members, function (eachMember) {
                    return _.isEqual(eachMember.orgSkin.skinid, skin.skinid)
                })

                this.REMOVE_MEMBER_FROM_TEAM({ member, teamId: this.team.id })
            },

            openChampionSkinDialog(skin) {
                this.targetSkin = skin

                this.dialogTableVisible = true
            },

            currentChangeHandle(val) {
                this.pagination.start = val
            },

            getSkinAttrValue(skin, attrName) {
                return SkinService.getSkinAttrValue(skin, attrName)
            },

            getAtkType(val) {
                return SkinService.getAtkType(val)
            },

            getSpecialAttrBonus(skin, attrName) {
                return SkinService.getSpecialAttrBonus(skin, attrName)
            },

            getSpecialAttrBonusStyle(skin, attrName) {
                let bonus = this.getSpecialAttrBonus(skin, attrName)

                if (_.isEqual(0, bonus)) {
                    return {
                        display: 'none',
                    }
                }

                let color = _.lt(0, bonus) ? '#13CE66' : '#FF4949';

                return {
                    color,
                    float: 'right',
                    'padding-right': '16px',
                    'font-weight': 'bold',
                }
            },

            getCardStyle(skin) {
                let url = require(`../../assets/char/${skin.skinid}.png`)

                return {
                    'background-image': `url('${url}')`,
                    height: '150px',
                    'background-size': '75%',
                    'background-repeat': 'no-repeat',
                    cursor: 'pointer',
                }
            },

            returnToTeamShow() {
                this.$router.push({ name: 'team-show', params: { id: this.$route.params.id } })
            },

            switchToNextSkin() {
                let index = _.findIndex(this.getMappedSkins, { skinid: this.targetSkin.skinid })

                if (_.isEqual(index, this.getMappedSkins.length - 1)) {
                    let allIndex = this.pagination.start * this.pagination.perPage

                    if (_.gte(allIndex, this.skins.length)) {
                        this.pagination.start = 1

                        return this.targetSkin = this.skins[0]
                    } else {
                        this.pagination.start ++

                        return this.targetSkin = this.skins[(this.pagination.start - 1) * this.pagination.perPage]
                    }
                }

                return this.targetSkin = this.getMappedSkins[index + 1]
            },

            switchToPrevSkin() {
                let index = _.findIndex(this.getMappedSkins, { skinid: this.targetSkin.skinid })

                if (_.isEqual(index, 0)) {
                    let allIndex = (this.pagination.start - 1) * this.pagination.perPage

                    if (_.lte(allIndex, 0)) {
                        this.pagination.start = Math.ceil(this.skins.length / this.pagination.perPage)

                        return this.targetSkin = this.skins[this.skins.length - 1]
                    } else {
                        this.pagination.start --

                        return this.targetSkin = this.skins[allIndex - 1]
                    }
                }

                return this.targetSkin = this.getMappedSkins[index - 1]
            },

            addToTeamMembers() {
                this.ADD_TEAM_MEMBER({ teamId: this.team.id, mockSkin: _.assign({}, this.targetSkin) })
            },

            removeMemberFromTeam(member) {
                this.REMOVE_MEMBER_FROM_TEAM({ member, teamId: this.team.id })
            },
        },
    ),

    computed: Object.assign(
        mapGetters([
            'getSearchedSkins',
            'getTeams',
            'getConditions',
        ]),

        {
            skins() {
                return this.getSearchedSkins
            },

            getMappedSkins() {
                if (_.isEmpty(this.skins)) {
                    return []
                }

                return this.skins.slice((this.pagination.start - 1) * this.pagination.perPage, this.pagination.start * this.pagination.perPage)
            },

            team() {
                let teamId = this.$route.params.id

                return _.find(this.getTeams, { id: teamId })
            },

            chapters() {
                return Object.keys(this.chapterMap)
            },
        },
    ),

    mounted() {
        this.INIT_TEAMS()

        this.jade = _.assign({}, SkinService.orgConditions).jade
        this.resource = _.assign({}, SkinService.orgConditions).resource
        this.selectedChapter = this.getConditions.chapter
    },

    components: {
        MemberAttrBoard,
        MemberCard,
        MemberSpellCardIntro,
        MemberImage,
        MemberHeadTitle,
    },
}
</script>

<style>

.champion-detail-info-alert {
    background-color: #f1f1f1;
    padding: 1px 1px 1px 10px;
    margin: 7px 15px 3px 7px;
    border-radius: 6px;
}

</style>


