<template>
    <div>
        <el-col :span="8">
            <i class="el-icon-caret-left" style="margin-top: 20px; line-height: 24px; float: left; font-size: 25px;" @click="switchToPrevSkin()"></i>
        </el-col>

        <el-col :span="8">
            <h3 style="text-align: center; margin-top: 20px; line-height: 24px;">{{ skin.skinname }} - {{ skin.card.cardname }}</h3>
        </el-col>

        <el-col :span="8">
            <i class="el-icon-caret-right" style="margin-top: 20px; line-height: 24px; float: right; font-size: 25px;" @click="switchToNextSkin()"></i>
        </el-col>
    </div>
</template>

<script>
export default {
    props: ['skin'],

    methods: {
        switchToPrevSkin() {
            return this.$emit('to-prev', this.skin)
        },

        switchToNextSkin() {
            return this.$emit('to-next', this.skin)
        },
    },
}
</script>


