<template>
    <el-row>
        <el-col :span="3">
            <b>信仰</b>
        </el-col>
        <el-col :span="2">
            <el-tag type="success">{{ getResCost.faith }}</el-tag>
        </el-col>

        <el-col :span="3">
            <b>食物</b>
        </el-col>
        <el-col :span="2">
            <el-tag type="warning">{{ getResCost.food }}</el-tag>
        </el-col>

        <el-col :span="3">
            <b>玉耗</b>
        </el-col>

        <el-col :span="2">
            <el-tag type="danger">{{ getJadeCost }}</el-tag>
        </el-col>

        <el-col :span="9">
            <button type="button" class="el-button el-button--default el-button--mini"
                v-clipboard="teamTextInfo" v-if="teamTextInfo" @success="showCopyMessage"
            >
                <span><i class="el-icon-document"></i>
                Copy
                </span>
            </button>
        </el-col>
    </el-row>
</template>

<script>

import { mapGetters } from 'vuex'

import TeamService from '../../../services/TeamService'

export default {
    props: ['team'],

    methods: {
        showCopyMessage() {
            this.$message({
                message: `${this.team.name} 资讯已复制到剪贴簿`,
                type: 'success'
            })
        },

        getAttrVal(member, attrName) {
            return TeamService.getDisplayAttrVal(member, attrName, { weapons: this.getWeapons, armors: this.getArmors })
        },
    },

    computed: Object.assign(
        mapGetters([
            'getWeapons',
            'getArmors',
        ]),

        {
            getJadeCost() {
                return TeamService.getTeamJadeCost(this.team)
            },

            getResCost() {
                return TeamService.getTeamResourceCost(this.team)
            },

            mapStrings() {
                let mapStrings = []

                _.forEach(this.team.maps, function (map) {
                    mapStrings.push(TeamService.getMapFormatString(map))
                })

                return mapStrings
            },

            teamTextInfo() {
                if (_.isNil(this.team) || _.isEmpty(TeamService.getStartingMembers(this.team))) {
                    return null
                }

                let info = `#队伍名称: ${this.team.name}`

                info += `\n\n#阵容`

                _.forEach(TeamService.getStartingMembers(this.team), function (member) {
                    let posTitle = TeamService.positionNames[member.position]
                    let weapon = _.find(this.getWeapons, { equipmentid: member.equipments.atk })
                    let armor = _.find(this.getArmors, { equipmentid: member.equipments.def })
                    let weaponName = _.isNil(weapon) ? '' : `${weapon.equipmentname}  `
                    let armorName = _.isNil(armor) ? '' : armor.equipmentname

                    info += `\n\n\t##${posTitle}: ${member.orgSkin.card.cardname}(${member.level})`
                    info += `\n\t\t-属性: 力量: ${this.getAttrVal(member, 'atk_mel')} 灵力: ${this.getAttrVal(member, 'atk_rang')} 暴击: ${this.getAttrVal(member, 'crit')} 命中: ${this.getAttrVal(member, 'hitrate')} 生命: ${this.getAttrVal(member, 'hp')} 防御: ${this.getAttrVal(member, 'def')} 回避: ${this.getAttrVal(member, 'avoid')} 幸运: ${this.getAttrVal(member, 'lucky')} 格档: ${this.getAttrVal(member, 'block')}`
                    info += `\n\t\t-装备: ${weaponName}${armorName}`
                }.bind(this))

                info += `\n\n#适用地图: \n\t${this.mapStrings.join(',')}`
                info += `\n\n#说明:`
                info += `\n\t${this.team.note}`

                return info
            },
        }
    ),
}
</script>


