 import skins from '../dataset/skins.js'

import MockSkinService from './MockSkinService'
import CommonService from './CommonService'

export default class TeamService
{
    static get mock()
    {
        // ATH004101 秋穰子, ATHZ02701 神绮, ATH000401 大妖精, ATH006701 村沙水蜜, ATHZ01901 幽香
        let s1 = MockSkinService.schema
        let s2 = MockSkinService.schema
        let m = MockSkinService.schema
        let d1 = MockSkinService.schema
        let d2 = MockSkinService.schema

        let team = TeamService.schema

        s1.orgSkin = _.find(skins, { skinid: 'ATH000401' })
        s2.orgSkin = _.find(skins, { skinid: 'ATH004101' })
        m.orgSkin = _.find(skins, { skinid: 'ATHZ02701' })
        d1.orgSkin = _.find(skins, { skinid: 'ATH006701' })
        d2.orgSkin = _.find(skins, { skinid: 'ATHZ01901' })

        team.name = 'Test'
        team.s1 = s1
        team.s2 = s2
        team.m = m
        team.d1 = d1
        team.d2 = d2
        team.maps = ['兽道e', '核熔炉n', '红魔馆前院h']

        return team
    }

    static get positions()
    {
        return ['s1', 's2', 'm', 'a1', 'a2', 'd1', 'd2']
    }

    static get positionNames()
    {
        return {
            s1: '辅助下位',
            s2: '辅助上位',
            m: '队长',
            a1: '攻击下位',
            a2: '攻击上位',
            d1: '防御下位',
            d2: '防御上位',
        }
    }

    static get schema()
    {
        return {
            id: CommonService.guid(),
            isteam: true,
            name: null,
            maps: [],
            note: null,
            members: [], // 数量限制 12 个, 和篮球一样
            s1: null,
            s2: null,
            m: null,
            a1: null,
            a2: null,
            d1: null,
            d2: null,
        }
    }

    static get attrs()
    {
        return {
            atk_mel: '力量',
            atk_rang: '灵力',
            hitrate: '命中',
            hp: '生命',
            def: '防御',
            avoid: '闪避',
            crit: '暴击',
            lucky: '幸运',
            block: '格档',
        }
    }

    static getDisplayAttrVal(member, attrName, { weapons, armors })
    {
        if (_.isNil(member.orgSkin)) {
            return null
        }

        let attrVal = Number(member.orgSkin.card[attrName]) + Number(member.bonus[attrName]) + Number(member.orgSkin[attrName])
        let equipmentAttrVal = 0
        let weapon = _.find(weapons, { equipmentid: member.equipments.atk })
        let armor = _.find(armors, { equipmentid: member.equipments.def })

        if (!_.isNil(weapon)) {
            equipmentAttrVal += Number(weapon[attrName])
        }

        if (!_.isNil(armor)) {
            equipmentAttrVal += Number(armor[attrName])
        }

        return attrVal + equipmentAttrVal
    }

    static getTeamJadeCost(team)
    {
        let jadeCost = 0
        let todoList = [
            {
                positions: ['s1', 's2'],
                spell: 'supSpell',
            },

            {
                positions: ['a1', 'a2', 'm'],
                spell: 'atkSpell',
            },

            {
                positions: ['d1', 'd2'],
                spell: 'defSpell',
            },
        ]

        function getSpellPoint(skin, spell) {
            return _.isNil(skin[spell]) || (isNaN(skin[spell].spell_point) && isNaN(skin[spell].spell_point.substr(0, 1)))
                ? 0 : (!isNaN(skin[spell].spell_point) ? Number(skin[spell].spell_point) : Number(skin[spell].spell_point.substr(0, 1)))
        }

        _.forEach(todoList, todo => {
            _.forEach(todo.positions, posName => {
                let member = team[posName]

                if (!_.isNil(member)) {
                    jadeCost += getSpellPoint(member.orgSkin, todo.spell)
                }
            })
        })

        return jadeCost
    }

    static getTeamResourceCost(team)
    {
        let faithCost = 0
        let foodCost = 0

        _.forEach(TeamService.getStartingMembers(team), member => {
            faithCost += Number(member.orgSkin.use_faith)
            foodCost += Number(member.orgSkin.use_food)
        })

        return {
            faith: faithCost,
            food: foodCost,
        }
    }

    static getStartingMembers(team)
    {
        let startingMembers = []

        _.forEach(team.members, function (member) {
            if (!_.isNil(member.position)) {
                startingMembers.push(member)
            }
        })

        return startingMembers
    }

    static getMapFormatString(map)
    {
        let mapArr = map.split('-')

        return _.isEqual(mapArr.length, 3) ? `${mapArr[1]}${mapArr[2].substr(0, 1)}` : null
    }
}


