export default class MockSkinService
{
    static get schema() {
        return {
            //teamid: null,
            position: null, // s1, s2, m , a1, a2, d1, d2
            orgSkin: null,
            level: 1,
            totalPoints: 0,
            usedPoints: 0,
            bonus: {
                atk_mel: 0,
                atk_rang: 0,
                hp: 0,
                def: 0,
                hitrate: 0,
                avoid: 0,
                crit: 0,
                lucky: 0,
                block: null,
            },
            equipments: {
                def: null,
                atk: null,
            },
        }
    }

    static calAddNeededPoint(attrVal) {
        return Math.ceil(Number(attrVal) / 10)
    }

    static calMinusReturnPoint(attrVal, isHp = null) {
        let _attrVal = isHp ? attrVal / 5 - 1: attrVal - 1

        return Math.ceil(Number(attrVal) / 10)
    }

    static isMax(mockSkin, attrName, isHp = null) {
        let factor = isHp ? 5 : 1
        let maxLimit = 100 * factor + Number(mockSkin.skin[attrName])
        let currentVal = Number(mockSkin[attrName]) + Number(mockSkin.bonus[attrName]) + Number(mockSkin.skin[attrName])

        return _.gte(currentVal, maxLimit)
    }

    static isOverMinusLimit() {

    }
}


