<template>
    <el-row :gutter="25">

        <el-col :xs="24" :sm="12" :md="12" :lg="8" style="margin-bottom: 12px; margin-top: 12px;">
            <el-card class="box-card">
                <div>
                    <div style="border-bottom: 0px;">
                        <el-input v-model="teamName" placeholder="输入队伍名称" style="width: 100%;">
                            <el-button slot="append" icon="plus" type="primary" @click="addNewTeam">
                            </el-button>
                        </el-input>
                    </div>
                </div>
            </el-card>
        </el-col>

        <el-col :xs="24" :sm="12" :md="12" :lg="8" v-if="getTeamsReverse" v-for="team in getTeamsReverse" :key="team.name" style="margin-bottom: 12px; margin-top: 12px;">
            <team-card v-show="isTeamShow(team)" :team="team"></team-card>
        </el-col>

        <el-col :xs="24" :sm="12" :md="12" :lg="8" v-if="getTeamsReverse.length == 0">
            <el-alert
                style="margin-bottom: 12px; margin-top: 12px;"
                title="目前没有任何队伍"
                type="info"
                description="请从左侧栏位输入队伍名称建立队伍"
                :closable="false"
                show-icon
            >
            </el-alert>
        </el-col>
    </el-row>
</template>

<script>

import { mapGetters, mapActions } from 'vuex'

import TeamCard from './Partials/Card'

import TeamService from '../../services/TeamService'

export default {
    data() {
        return {
            teamName: null,
            error: null,
        }
    },

    computed: Object.assign(
        mapGetters([
            'getSkins',
            'getTeamsReverse',
        ]),
    ),

    methods: Object.assign(
        mapActions([
            'INIT_TEAMS',
            'CREATE_TEAM',
        ]),

        {
            addNewTeam() {
                if (_.gt(1, this.teamName)) {
                    return false
                }

                this.CREATE_TEAM(this.teamName)

                return this.teamName = null
            },

            isTeamShow(team) {
                if (_.isEmpty(this.teamName)) {
                    return true
                }

                return _.includes(team.name, this.teamName)
            },
        },
    ),

    components: {
        TeamCard,
    },

    mounted() {
        this.INIT_TEAMS(true)
    },
}
</script>


