<template>
    <div>
        <el-row :gutter="1" style="margin-top: 5px;">
            <div @click="clickHandle('s2')">
                <el-col :span="8" :style="checkBoardClass(60, team.s2)">
                    <div :style="genBkImgStyle(team.s2)">
                        &nbsp;
                    </div>
                </el-col>
            </div>

            <div @click="clickHandle('a2')">
                <el-col :span="8" :style="checkBoardClass(0, team.a2)">
                    <div :style="genBkImgStyle(team.a2)">
                        &nbsp;
                    </div>
                </el-col>
            </div>

            <div @click="clickHandle('d2')">
                <el-col :span="8" :style="checkBoardClass(60, team.d2)">
                    <div :style="genBkImgStyle(team.d2)">
                        &nbsp;
                    </div>
                </el-col>
            </div>
        </el-row>

        <el-row :gutter="1">
            <el-col :span="8" :style="checkBoardClass(0, null, 'isEmpty')"></el-col>

            <div @click="clickHandle('m')">
                <el-col :span="8" :style="checkBoardClass(0, team.m)">
                    <div :style="genBkImgStyle(team.m)">
                        &nbsp;
                    </div>
                </el-col>
            </div>

            <el-col :span="8" :style="checkBoardClass(0, null, 'isEmpty')"></el-col>
        </el-row>

        <el-row :gutter="1">
            <div @click="clickHandle('s1')">
                <el-col :span="8" :style="checkBoardClass(-60, team.s1)">
                    <div :style="genBkImgStyle(team.s1)">
                        &nbsp;
                    </div>
                </el-col>
            </div>

            <div @click="clickHandle('a1')">
                <el-col :span="8" :style="checkBoardClass(0, team.a1)">
                    <div :style="genBkImgStyle(team.a1)">
                        &nbsp;
                    </div>
                </el-col>
            </div>

            <div @click="clickHandle('d1')">
                <el-col :span="8" :style="checkBoardClass(-60, team.d1)">
                    <div :style="genBkImgStyle(team.d1)">
                        &nbsp;
                    </div>
                </el-col>
            </div>
        </el-row>
    </div>
</template>

<script>
export default {
    props: {
        team: {
            type: Object,
            default: {},
        },

        editMember: {
            type: Object,
            default: null,
        },
    },

    methods: {
        genBkImgStyle(member) {
            if (_.isNil(member) || _.isNil(member.orgSkin)) {
                return {
                    position: 'absolute',
                    'background-repeat': 'no-repeat',
                    margin: '0px auto',
                }
            }

            let frame = member.orgSkin.frame
            let width = frame.rotated ? frame.frame[1][1] : frame.frame[1][0]
            let height = frame.rotated ? frame.frame[1][0] : frame.frame[1][1]
            let url = require(`../../../assets/${frame.from}.png`)

            return {
                position: 'absolute',
                'background-repeat': 'no-repeat',
                margin: '0px auto',
                'background-image': `url('${url}')`,
                'background-position': `-${frame.frame[0][0]}px -${frame.frame[0][1]}px`,
                width: `${width}px`,
                height: `${height}px`,
                transform: frame.rotated ? 'scale(0.7) rotate(-90deg)' : 'scale(0.7)',
                right: `-${(width - 100)/2}px`,
                bottom: `-${(height - 100)/2}px`,
            }
        },

        checkBoardClass(top, member = null, isEmpty = null) {
            return _.isNull(isEmpty) ? {
                'z-index': 99,
                position: 'relative',
                top: `${top}px`,
                margin: '2px 5px 2px 5px',
                width: '100px',
                height: '100px',
                'background-color': !_.isNull(member) && !_.isNull(this.editMember) && _.isEqual(this.editMember.orgSkin.skinid, member.orgSkin.skinid) ? '#1F2D3D' : '#E5E9F2',
            } : {
                margin: '5px 5px 5px 5px',
                'width': '100px',
                'height': '100px',
                'background-color': 'white',
            }
        },

        clickHandle(val) {
            this.$emit('click', val)
        },

        save() {
            this.$emit('save')

            this.removes = null
        },

        clearAll() {
            this.$emit('clear-all')
        },
    },
}
</script>


