import skins from '../../dataset/skins.js'
import SkinService from '../../services/SkinService'

let state = {
    skins,
    conditions: SkinService.orgConditions,
}

let getters = {
    getSkins(state) {
        return state.skins
    },

    getConditions(state) {
        return state.conditions
    },

    getSearchedSkins(state) {
        let skins = state.skins
        let conditions = state.conditions

        if (!_.isNull(conditions.jade.type) && !_.isEmpty(conditions.jade.type)) {
            skins = _.filter(skins, skin => {
                let jadeCost = _.isNil(skin[conditions.jade.type]) ? 0 : Number(skin[conditions.jade.type].spell_point.toString().substr(0, 1))

                return _.gte(jadeCost, conditions.jade.cost[0]) && _.lte(jadeCost, conditions.jade.cost[1])
            })
        }

        if (!_.isNull(conditions.resource.type) && _.lt(0, conditions.resource.type)) {
            skins = _.filter(skins, skin => {
                let resourceCost = Number(skin.use_faith) * (Number(conditions.resource.type) & 1) + Number(skin.use_food) * (Number(conditions.resource.type) & 2)/2

                return _.gte(resourceCost, conditions.resource.cost[0]) && _.lte(resourceCost, conditions.resource.cost[1])
            })
        }

        let chapterMembers = []

        if (_.isEqual('全部', conditions.chapter)) {
            return skins
        }

        if (_.isEqual('其他', conditions.chapter)) {
            let flattenChapter = []

            _.forEach(SkinService.chapterMap, chapter => {
                flattenChapter.push(chapter)
            })

            flattenChapter = _.flattenDeep(flattenChapter)

            return _.filter(skins, skin => {
                return !_.includes(flattenChapter, skin.card.cardid)
            })
        }

        return _.filter(skins, skin => {
            return _.includes(SkinService.chapterMap[`东方${conditions.chapter}`], skin.card.cardid)
        })
    },
}

let actions = {
    SET_SEARCH_CONDITIONS({ commit }, { jade, resource, chapter }) {
        return commit('SET_SEARCH_CONDITIONS', { jade, resource, chapter })
    },
}

let mutations = {
    SET_SEARCH_CONDITIONS(state, { jade, resource, chapter }) {
        state.conditions.jade = jade
        state.conditions.resource = resource
        state.conditions.chapter = chapter
    },
}

export default {
    state,
    getters,
    actions,
    mutations,
}


