<template>
    <div style="display: inline-block;">
        <span v-if="attrVal > 0" class="attr_bonus_positive">
            +{{ attrVal }}
        </span>

        <span v-if="attrVal < 0" class="attr_bonus_negative">
            {{ attrVal }}
        </span>
    </div>
</template>

<script>
export default {
    props: ['attrVal'],
}
</script>

<style>

.attr_bonus_positive {
    color: #13CE66;
    font-size: 14px;
    font-weight: bold;
}

.attr_bonus_negative {
    color: #FF4949;
    font-size: 14px;
    font-weight: bold;
}

</style>


