<template>
    <div>
        <h3>
            {{ editMember.orgSkin.card.cardname }}

            <small v-if="editMember.orgSkin.skinname" style="font-weight: 200;"> {{ editMember.orgSkin.skinname }}</small>
        </h3>

        <el-row>
            <el-col :span="6">
                <b>信仰:</b>
            </el-col>

            <el-col :span="6">
                <el-tag type="success">{{ editMember.orgSkin.use_faith }}</el-tag>
            </el-col>

            <el-col :span="6">
                <b>饭团:</b>
            </el-col>

            <el-col :span="6">
                <el-tag type="warning">{{ editMember.orgSkin.use_food }}</el-tag>
            </el-col>

            <el-col :span="24" class="champion-attr-info-border">
                <el-progress :show-text="false" :stroke-width="10" :status="getCostPercentageStyle" :percentage="getCostPercentage"></el-progress>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="24">
                <el-button type="primary" @click="dialogVisible = true">
                    开启试算面板
                </el-button>

                <span style="color: #1D8CE0; font-size: 18px;">{{ editMember.totalPoints }} </span> =
                <span style="color: #FF4949; font-size: 18px;"> {{ editMember.usedPoints }}</span> +
                <span style="color: #13CE66; font-size: 18px;"> {{ availablePoints }}</span>
            </el-col>
        </el-row>

        <el-row :gutter="3" style="margin-bottom: 20px;">
            <el-col :span="3"><b>等级:</b></el-col>
            <el-col :span="9">
                <el-input-number v-model="editMember.level" @change="handleLevelChange" :min="1" :max="99" :step="1" :debounce="100"></el-input-number>
            </el-col>
            <el-col :span="12">&nbsp;</el-col>
        </el-row>

        <el-row style="height: 30px;">
            <el-col :md="3" :xs="3"><b>类型</b></el-col>
            <el-col :md="9" :xs="21">
                {{ getAtkType(editMember.orgSkin.atktype) }}
            </el-col>

            <el-col :md="3" :xs="10">
                <b>格档</b>

                <attr-bonus :attr-val="editMember.orgSkin.block"></attr-bonus>

                <span style="font-size: 14px;">{{ getBlockRate(getAttrTotalVal('block')) }}%触发</span>
            </el-col>
            <el-col :md="9" :xs="10">
                {{ getAttrTotalVal('block') }}

                <attr-bonus :attr-val="equipmentAttr('block')"></attr-bonus>
            </el-col>
        </el-row>

        <el-row>
            <el-col :md="3" :xs="5">
                <b>力量</b>

                <attr-bonus :attr-val="editMember.orgSkin.atk_mel"></attr-bonus>
            </el-col>
            <el-col :md="1" :xs="5">
                {{ getAttrTotalVal('atk_mel') }}
            </el-col>

            <el-col :md="8" :xs="14">
                <el-input-number v-model="editMember.bonus.atk_mel" @change="handleAtkMelChange" :min="0" :max="calculateAttrMax('atk_mel')" size="small" :step="1"></el-input-number>

                ({{ getPointsNeedPerStep('atk_mel', true) }})

                <attr-bonus :attr-val="equipmentAttr('atk_mel')"></attr-bonus>
            </el-col>

            <el-col :md="3" :xs="5">
                <b>灵力</b>

                <attr-bonus :attr-val="editMember.orgSkin.atk_rang"></attr-bonus>
            </el-col>
            <el-col :md="1" :xs="5">{{ getAttrTotalVal('atk_rang') }}</el-col>
            <el-col :md="8" :xs="14">
                <el-input-number v-model="editMember.bonus.atk_rang" @change="handleAtkRangChange" :min="0" :max="calculateAttrMax('atk_rang')" size="small" :step="1"></el-input-number>

                ({{ getPointsNeedPerStep('atk_rang', true) }})

                <attr-bonus :attr-val="equipmentAttr('atk_rang')"></attr-bonus>
            </el-col>
        </el-row>

        <el-row>
            <el-col :md="3" :xs="5">
                <b>生命</b>

                <attr-bonus :attr-val="editMember.orgSkin.hp"></attr-bonus>
            </el-col>
            <el-col :md="1" :xs="5">{{ getAttrTotalVal('hp') }}</el-col>
            <el-col :md="8" :xs="14">
                <el-input-number v-model="editMember.bonus.hp" @change="handleHpChange" :min="0" :max="calculateAttrMax('hp')" size="small" :step="5"></el-input-number>

                ({{ getPointsNeedPerStep('hp', true) }})

                <attr-bonus :attr-val="equipmentAttr('hp')"></attr-bonus>
            </el-col>

            <el-col :md="3" :xs="8">
                <b>防御</b>

                <attr-bonus :attr-val="editMember.orgSkin.def"></attr-bonus>

                <span style="font-size: 14px;">{{ getDefDiscount(getAttrTotalVal('def')) }}%减免</span>
            </el-col>
            <el-col :md="1" :xs="3">{{ getAttrTotalVal('def') }}</el-col>
            <el-col :md="8" :xs="13">
                <el-input-number v-model="editMember.bonus.def" @change="handleDefChange" :min="0" :max="calculateAttrMax('def')" size="small" :step="1"></el-input-number>

                ({{ getPointsNeedPerStep('def', true) }})

                <attr-bonus :attr-val="equipmentAttr('def')"></attr-bonus>
            </el-col>
        </el-row>

        <el-row>
            <el-col :md="3" :xs="5">
                <b>命中</b>

                <attr-bonus :attr-val="editMember.orgSkin.hitrate"></attr-bonus>
            </el-col>
            <el-col :md="1" :xs="5">{{ getAttrTotalVal('hitrate') }}</el-col>
            <el-col :md="8" :xs="14">
                <el-input-number v-model="editMember.bonus.hitrate" @change="handleHitrateChange" :min="0" :max="calculateAttrMax('hitrate')" size="small" :step="1"></el-input-number>

                ({{ getPointsNeedPerStep('hitrate', true) }})

                <attr-bonus :attr-val="equipmentAttr('hitrate')"></attr-bonus>
            </el-col>

            <el-col :md="3" :xs="5">
                <b>回避</b>

                <attr-bonus :attr-val="editMember.orgSkin.avoid"></attr-bonus>
            </el-col>
            <el-col :md="1" :xs="5">{{ getAttrTotalVal('avoid') }}</el-col>
            <el-col :md="8" :xs="14">
                <el-input-number v-model="editMember.bonus.avoid" @change="handleAvoidChange" :min="0" :max="calculateAttrMax('avoid')" size="small" :step="1"></el-input-number>

                ({{ getPointsNeedPerStep('avoid', true) }})

                <attr-bonus :attr-val="equipmentAttr('avoid')"></attr-bonus>
            </el-col>
        </el-row>

        <el-row>
            <el-col :md="3" :xs="5">
                <b>暴击</b>

                <attr-bonus :attr-val="editMember.orgSkin.crit"></attr-bonus>
            </el-col>
            <el-col :md="1" :xs="5">{{ getAttrTotalVal('crit') }}</el-col>
            <el-col :md="8" :xs="14">
                <el-input-number v-model="editMember.bonus.crit" @change="handleCritChange" :min="0" :max="calculateAttrMax('crit')" size="small" :step="1"></el-input-number>

                ({{ getPointsNeedPerStep('crit', true) }})

                <attr-bonus :attr-val="equipmentAttr('crit')"></attr-bonus>
            </el-col>

            <el-col :md="3" :xs="5">
                <b>幸运</b>

                <attr-bonus :attr-val="editMember.orgSkin.lucky"></attr-bonus>
            </el-col>
            <el-col :md="1" :xs="5">{{ getAttrTotalVal('lucky') }}</el-col>
            <el-col :md="8" :xs="14">
                <el-input-number v-model="editMember.bonus.lucky" @change="handleLuckyChange" :min="0" :max="calculateAttrMax('lucky')" size="small" :step="1"></el-input-number>

                ({{ getPointsNeedPerStep('lucky', true) }})

                <attr-bonus :attr-val="equipmentAttr('lucky')"></attr-bonus>
            </el-col>
        </el-row>

        <el-row :gutter="3">
            <el-col :md="3" :xs="3"><b>武器</b></el-col>
            <el-col :md="5" :xs="21">
                <el-select v-model="editMember.equipments.atk" filterable clearable placeholder="请选择">
                    <el-option
                    v-for="weapon in getWeapons"
                    :key="weapon.equipmentid"
                    :label="weapon.equipmentname"
                    :value="weapon.equipmentid">
                        <span style="float: left">{{ weapon.equipmentname }}</span>
                        <span style="float: right; color: #8492a6; font-size: 13px">
                            {{ weapon.property }}
                        </span>
                    </el-option>
                </el-select>
            </el-col>
            <el-col :md="4" :xs="24">&nbsp;</el-col>

            <el-col :md="3" :xs="3">
                <b>防具</b>
            </el-col>
            <el-col :md="5" :xs="21">
                <el-select v-model="editMember.equipments.def" filterable clearable placeholder="请选择">
                    <el-option
                    v-for="armor in getArmors"
                    :key="armor.equipmentid"
                    :label="armor.equipmentname"
                    :value="armor.equipmentid">
                        <span style="float: left">{{ armor.equipmentname }}</span>
                        <span style="float: right; color: #8492a6; font-size: 13px">
                            {{ armor.property }}
                        </span>
                    </el-option>
                </el-select>
            </el-col>
            <el-col :md="4" :xs="4">&nbsp;</el-col>
        </el-row>

        <el-dialog
            title="小算盘"
            :visible.sync="dialogVisible"
            size="large"
        >
            <calculate></calculate>
        </el-dialog>
    </div>
</template>

<script>

import { mapGetters } from 'vuex'

import SkinService from '../../../services/SkinService'

import AttrBonus from '../Widget/AttrBonus'
import Calculate from '../Widget/Calculate'

export default {
    props: ['editMember'],

    data() {
        return {
            dialogVisible: false,
        }
    },

    methods: {
        getAtkType(val) {
            return SkinService.getAtkType(val)
        },

        getDefDiscount(val) {
            return 100 - Math.round(100 * 120/(120 + val))
        },

        getBlockRate(val) {
            return Math.round(100 * val/(val + 90))
        },

        handleAtkMelChange(val) {
            this.handleAttrChange(val, 'atk_mel')
        },

        handleAtkRangChange(val) {
            this.handleAttrChange(val, 'atk_rang')
        },

        handleHpChange(val) {
            this.handleAttrChange(val, 'hp')
        },

        handleDefChange(val) {
            this.handleAttrChange(val, 'def')
        },

        handleHitrateChange(val) {
            this.handleAttrChange(val, 'hitrate')
        },

        handleAvoidChange(val) {
            this.handleAttrChange(val, 'avoid')
        },

        handleCritChange(val) {
            this.handleAttrChange(val, 'crit')
        },

        handleLuckyChange(val) {
            this.handleAttrChange(val, 'lucky')
        },

        handleAttrChange(val, attrName) {
            let orgSkinAttrVal = this.editMember.orgSkin.card[attrName]
            let oldTotalAttrVal = Number(this.editMember.orgSkin.card[attrName]) + Number(this.editMember.bonus[attrName])
            let newTotalAttrVal = Number(this.editMember.orgSkin.card[attrName]) + Number(val)
            let totalDistancePoints = this.getUsedPointsInRange(orgSkinAttrVal, newTotalAttrVal, _.isEqual(attrName, 'hp')) - this.getUsedPointsInRange(orgSkinAttrVal, oldTotalAttrVal, _.isEqual(attrName, 'hp'))

            this.editMember.usedPoints += Number(totalDistancePoints)
        },

        handleLevelChange(level) {
            if (_.lt(level, this.editMember.level)) {
                this.editMember.usedPoints = 0

                _.forEach(this.editMember.bonus, function (eachBonus, key) {
                    this.editMember.bonus[key] = 0
                }.bind(this))
            }

            this.editMember.totalPoints = (level - 1) * 5
        },

        getAttrTotalVal(attrName) {
            let attrVal = Number(this.editMember.orgSkin.card[attrName]) + Number(this.editMember.bonus[attrName]) + Number(this.editMember.orgSkin[attrName])
            let equipmentAttrVal = 0
            let weapon = _.find(this.getWeapons, { equipmentid: this.editMember.equipments.atk })
            let armor = _.find(this.getArmors, { equipmentid: this.editMember.equipments.def })

            if (!_.isNil(weapon)) {
                equipmentAttrVal += Number(weapon[attrName])
            }

            if (!_.isNil(armor)) {
                equipmentAttrVal += Number(armor[attrName])
            }

            return attrVal + equipmentAttrVal
        },

        getPointsNeedPerStep(attrName, isPositive) {
            let factor = _.isEqual(attrName, 'hp') ? 5 : 1
            let orgAttrVal = Number(this.editMember.orgSkin.card[attrName])
            let currentAttrVal = orgAttrVal + Number(this.editMember.bonus[attrName])
            let needPoints = Math.floor((currentAttrVal - (isPositive ? 0 : 1 ))/factor/10) + 1

            return _.gt(needPoints, 10) ? 'M' : needPoints
        },

        getUsedPointsInRange(srcAttrVal, destAttrVal, isHp) {
            let factor = _.isEqual(true, isHp) ? 5 : 1
            let totalNeedPoint = 0

            for (let i = srcAttrVal/factor; i < destAttrVal/factor; i ++) {
                let eachNeedPoints = Math.floor(i/10) + 1

                totalNeedPoint += eachNeedPoints
            }

            return totalNeedPoint
        },

        calculateAttrMax(attrName) {
            let factor = _.isEqual(attrName, 'hp') ? 5 : 1
            let maxBonus = 0
            let costPoints = 0
            let srcAttrVal = Number(this.editMember.orgSkin.card[attrName]) + Number(this.editMember.bonus[attrName])

            for (let i = srcAttrVal/factor; i < 100; i ++) {
                let cost = Math.floor(i/10) + 1

                if (_.gt(costPoints + cost, this.availablePoints)) {
                    return maxBonus + Number(this.editMember.bonus[attrName])
                }

                maxBonus += factor
                costPoints += cost
            }

            return maxBonus + Number(this.editMember.bonus[attrName])
        },

        equipmentAttr(attrName) {
            let weapon = _.find(this.getWeapons, { equipmentid: this.editMember.equipments.atk })
            let armor = _.find(this.getArmors, { equipmentid: this.editMember.equipments.def })

            return (_.isNil(weapon) ? 0 : Number(weapon[attrName])) + (_.isNil(armor) ? 0 : Number(armor[attrName]))
        },
    },

    computed: Object.assign(
        mapGetters([
            'getWeapons',
            'getArmors',
        ]),

        {
            availablePoints() {
                return Number(this.editMember.totalPoints) - Number(this.editMember.usedPoints)
            },

            getCostPercentage() {
                let totalCost = Number(this.editMember.orgSkin.use_faith) + Number(this.editMember.orgSkin.use_food)
                let percentage = Math.ceil(totalCost/30 * 100)

                return percentage
            },

            getCostPercentageStyle() {
                let val = this.getCostPercentage

                if (_.gt(33, val)) {
                    return 'success'
                }

                if (_.gt(66, val)) {
                    return ''
                }

                return 'exception'
            },

            editHp() {
                return _.isNil(this.editMember) ? 0 : this.editMember.bonus.hp
            },
        },
    ),

    watch: {
        editHp(val) {
            if (isNaN(val) || _.isEqual(0, val % 5)) {
                return
            }

            this.editMember.bonus.hp = val - val % 5
        }
    },

    components: {
        AttrBonus,
        Calculate,
    },
}
</script>

<style>

.champion-attr-info-border {
    margin-top: 15px;
    margin-bottom: 15px;
}
</style>



