import EquipmentService from '../../services/EquipmentService'

const state = {
    weapons: EquipmentService.weapons,
    armors: EquipmentService.armors,
}

const getters = {
    getWeapons(state) {
        return _.orderBy(state.weapons, ['sellgold', 'equipmentname'], ['desc', 'asc'])
    },

    getArmors(state) {
        return _.orderBy(state.armors, ['sellgold', 'equipmentname'], ['desc', 'asc'])
    },
}

export default {
    state,
    getters,
}


