<template>
    <el-card class="box-card" style="margin-right: 5px; height: 600px;">
        <div slot="header" class="clearfix">
            <span style="line-height: 36px;">
                {{ team.name }}
            </span>

            <el-button style="float: right;" type="info" @click="routeLinkTo">
                <i class="el-icon-arrow-right"></i>
            </el-button>
        </div>

        <team-cost :team="team"></team-cost>

        <el-tabs v-model="activeName">

            <el-tab-pane label="先发阵容" name="first">
                <team-quota-position :team="team"></team-quota-position>

                <div>
                    <el-tag :type="getMapType(map)" style="margin-right: 5px;" v-for="map in team.maps" :key="map">
                        {{ getMapFormatName(map) }}
                    </el-tag>
                </div>
            </el-tab-pane>

            <el-tab-pane label="队伍说明" name="second">
                <div style="margin-top: 20px; margin-bottom: 20px;">
                    {{ team.note }}
                </div>

                <el-alert
                    v-if="!team.note || team.note.length == 0"
                    title="目前没有关于此队伍的说明"
                    type="warning"
                    description="此队伍目前没有任何介绍"
                    :closable="false"
                    show-icon
                ></el-alert>
            </el-tab-pane>

            <el-tab-pane label="先发资讯" name="third">
                <el-alert
                    v-if="!startingMembers || startingMembers.length == 0"
                    title="目前没有任何角色先发"
                    type="warning"
                    description="请安排角色至先发位置"
                    :closable="false"
                    show-icon
                >
                </el-alert>

                <el-table
                    v-if="startingMembers.length > 0"
                    :data="startingMembers"
                    border
                    style="width: 100%"
                >
                    <el-table-column
                        fixed
                        label="人物"
                        width="150"
                    >
                        <template scope="scope">
                            {{ scope.row.orgSkin.card.cardname }} ({{ scope.row.level }})

                            <el-tag v-if="scope.row.equipments.atk">
                                {{ getWeapon(scope.row.equipments.atk).equipmentname }}
                            </el-tag>

                            <el-tag v-if="scope.row.equipments.def">
                                {{ getArmor(scope.row.equipments.def).equipmentname }}
                            </el-tag>
                        </template>
                    </el-table-column>

                    <el-table-column v-for="(eachAttr, attrName) in attrs"
                        :label="eachAttr"
                        width="70"
                        :key="eachAttr"
                    >
                        <template scope="scope">
                            {{ getMemberDisplayAttrVal(scope.row, attrName) }}
                        </template>
                    </el-table-column>

                </el-table>
            </el-tab-pane>

        </el-tabs>
    </el-card>
</template>

<script>

import { mapGetters } from 'vuex'

import TeamService from '../../../services/TeamService'

import TeamQuotaPosition from './TeamQuotaPosition'
import TeamCost from '../Widget/TeamCost'

export default {
    props: ['team'],

    data() {
        return {
            mapLevelColorMap: {
                easy: 'success',
                normal: 'primary',
                hard: 'danger',
            },

            activeName: 'first',
        }
    },

    methods: {
        routeLinkTo() {
            this.$router.push({
                name: 'team-show',

                params: {
                    id: this.team.id,
                },
            })
        },

        getMapType(map) {
            let mapArr = map.split('-')

            return _.isEqual(mapArr.length, 3) ? this.mapLevelColorMap[mapArr[2]] : null
        },

        getMapFormatName(map) {
            return TeamService.getMapFormatString(map)
        },

        getMemberDisplayAttrVal(member, attrName, weapons, armors) {
            return TeamService.getDisplayAttrVal(member, attrName, { weapons: this.getWeapons, armors: this.getArmors })
        },

        getWeapon(equipmentid) {
            return _.find(this.getWeapons, { equipmentid })
        },

        getArmor(equipmentid) {
            return _.find(this.getArmors, { equipmentid })
        },
    },

    computed: Object.assign(
        mapGetters([
            'getWeapons',
            'getArmors',
        ]),

        {
            skin() {
                return this.$store.getters.getSkins[0]
            },

            attrs() {
                return TeamService.attrs
            },

            startingMembers() {
                return TeamService.getStartingMembers(this.team)
            },
        }
    ),

    components: {
        TeamQuotaPosition,
        TeamCost,
    },
}

</script>


