import TeamService from  '../../services/TeamService'
import MockSkinService from '../../services/MockSkinService'

let state = {
    hasInitialTeams: false,
    teams: [],
    errors: [],
    positions: TeamService.positions,
}

let getters = {
    getTeams(state) {
        return state.teams
    },

    getTeamsReverse(state) {
        return state.teams.slice().reverse()
    },

    getTeamErrors(state) {
        return state.errors
    },

    getPositions(state) {
        return state.positions
    },
}

let actions = {
    INIT_TEAMS({ commit, dispatch, state }, force) {
        if (_.isEqual(true, state.hasInitialTeams) && !_.isEqual(true, force)) {
            return dispatch('ADD_TEAM_ERRORS', '隊伍已經初始過了!')
        }

        return commit('INIT_TEAMS')
    },

    CREATE_TEAM({ commit }, name) {
        if (!_.isNil(localStorage.getItem(name))) {
            return
        }

        let team = TeamService.schema

        team.name = name

        return commit('CREATE_TEAM', team)
    },

    UPDATE_TEAM({ commit, dispatch }, team) {
        commit('UPDATE_TEAM', team)
    },

    DESTROY_TEAM({ commit, dispatch }, team) {
        commit('DESTROY_TEAM', team)
        dispatch('INIT_TEAMS', true)
    },

    ADD_TEAM_ERRORS({ commit }, error) {
        return commit('ADD_TEAM_ERRORS', error)
    },

    EMPTY_TEAM_ERRORS({ commit }) {
        return commit('EMPTY_TEAM_ERRORS')
    },

    ADD_TEAM_MEMBER({ commit }, { teamId, mockSkin }) {
        return commit('ADD_TEAM_MEMBER', { teamId, mockSkin })
    },

    REMOVE_MEMBER_FROM_TEAM({ commit }, { teamId, member }) {
        return commit('REMOVE_MEMBER_FROM_TEAM', { teamId, member })
    },
}

let mutations = {
    ADD_TEAM_MEMBER(state, { teamId, mockSkin }) {
        let teamIndex = _.findIndex(state.teams, { id: teamId })

        // 找不到對應隊伍不處理
        if (_.isEqual(-1, teamIndex)) {
            return
        }

        let team = state.teams[teamIndex]

        let existsMember = _.find(team.members, member => {
            return _.isEqual(member.orgSkin.skinid, mockSkin.skinid)
        })

        // 若已存在隊伍中不處理
        if (!_.isNil(existsMember)) {
            return
        }

        // 加入成員到隊伍中
        team.members.push(_.assign({}, MockSkinService.schema, { orgSkin: mockSkin }))

        // 更新 DataPersist
        localStorage.setItem(team.id, JSON.stringify(team))

        // 觸發 Vue 更新
        state.teams.splice(teamIndex, 1, team)
    },

    UPDATE_TEAM(state, team) {
        let teamIndex = _.findIndex(state.teams, { id: team.id })

        // 更新 DataPersist
        localStorage.setItem(team.id, JSON.stringify(team))

        // 觸發 Vue 更新
        state.teams.splice(teamIndex, 1, team)
    },

    REMOVE_MEMBER_FROM_TEAM(state, { teamId, member }) {
        let teamIndex = _.findIndex(state.teams, { id: teamId })

        // 找不到對應隊伍不處理
        if (_.isEqual(-1, teamIndex)) {
            return
        }

        let team = state.teams[teamIndex]

        // 若在先發名單不可移除
        if (!_.isNil(member.position)) {
            return
        }

        let memberIndex = _.findIndex(team.members, eachMember => {
            return _.isEqual(member.orgSkin.skinid, eachMember.orgSkin.skinid)
        })

        // 若不存在此成員則不進行動作
        if (_.isEqual(-1, memberIndex)) {
            return
        }

        // 從隊伍移除成員
        team.members.splice(memberIndex, 1)

        // 更新 DataPersist
        localStorage.setItem(team.id, JSON.stringify(team))

        // 觸發 Vue 更新
        state.teams.splice(teamIndex, 1, team)
    },

    ADD_TEAM_ERRORS(state, error) {
        state.errors.push(error)
    },

    EMPTY_TEAM_ERRORS(state) {
        state.errors = []
    },

    CREATE_TEAM(state, team) {
        localStorage.setItem(team.id, JSON.stringify(team))

        state.teams.push(team)
    },

    DESTROY_TEAM(state, team) {
        localStorage.removeItem(team.id)
    },

    INIT_TEAMS(state) {
        state.teams = []

        let keys = Object.keys(localStorage)

        for (let i = 0; i < keys.length; i ++) {
            try {
               let tmpTeam = JSON.parse(localStorage.getItem(keys[i]))

               if (_.isEqual(true, tmpTeam.isteam)) {
                    state.teams.push(tmpTeam)
                }
            } catch(e) {
               continue
            }
        }

        state.teams.splice(state.teams.length)
        state.hasInitialTeams = true
    },
}

export default {
    state,
    getters,
    actions,
    mutations,
}


