<template>
    <el-row :gutter="3" :style="getFontStyle">
        <el-col :span="6">
            <b>信仰:</b>
        </el-col>

        <el-col :span="6">
            <el-tag type="success">{{ skin.use_faith }}</el-tag>
        </el-col>

        <el-col :span="6">
            <b>饭团:</b>
        </el-col>

        <el-col :span="6">
            <el-tag type="warning">{{ skin.use_food }}</el-tag>
        </el-col>

        <el-col :span="24" class="champion-attr-info-border">
            <el-progress :show-text="false" :stroke-width="10" :status="getCostPercentageStyle" :percentage="getCostPercentage"></el-progress>
        </el-col>

        <el-col :span="6">
            <b>类型:</b>
        </el-col>

        <el-col :span="6">
            {{ getAtkType(skin.atktype) }}
        </el-col>

        <el-col :span="6">
            <b>格档:</b>

            <span :style="getSpecialAttrBonusStyle('block')">
                <span v-if="getSpecialAttrBonus('block') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('block') }}
            </span>
        </el-col>

        <el-col :span="6">
            {{ getSkinAttrValue('block') }}
        </el-col>

        <el-col :span="6">
            <b>力量:</b>

            <span :style="getSpecialAttrBonusStyle('atk_mel')">
                <span v-if="getSpecialAttrBonus('atk_mel') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('atk_mel') }}
            </span>
        </el-col>

        <el-col :span="6">
            {{ getSkinAttrValue('atk_mel') }}
        </el-col>

        <el-col :span="6">
            <b>灵力:</b>

            <span :style="getSpecialAttrBonusStyle('atk_rang')">
                <span v-if="getSpecialAttrBonus('atk_rang') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('atk_rang') }}
            </span>
        </el-col>

        <el-col :span="6">
            {{ getSkinAttrValue('atk_rang') }}
        </el-col>

        <el-col :span="6">
            <b>生命:</b>

            <span :style="getSpecialAttrBonusStyle('hp')">
                <span v-if="getSpecialAttrBonus('hp') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('hp') }}
            </span>
        </el-col>

        <el-col :span="6">
            {{ getSkinAttrValue('hp') }}
        </el-col>

        <el-col :span="6">
            <b>防御:</b>

            <span :style="getSpecialAttrBonusStyle('def')">
                <span v-if="getSpecialAttrBonus('def') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('def') }}
            </span>
        </el-col>

        <el-col :span="6" >
            {{ getSkinAttrValue('def') }}
        </el-col>

        <el-col :span="6">
            <b>命中:</b>

            <span :style="getSpecialAttrBonusStyle('hitrate')">
                <span v-if="getSpecialAttrBonus('hitrate') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('hitrate') }}
            </span>
        </el-col>

        <el-col :span="6">
            {{ getSkinAttrValue('hitrate') }}
        </el-col>

        <el-col :span="6">
            <b>回避:</b>

            <span :style="getSpecialAttrBonusStyle('avoid')">
                 <span v-if="getSpecialAttrBonus('avoid') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('avoid') }}
            </span>
        </el-col>

        <el-col :span="6">
            {{ getSkinAttrValue('avoid') }}
        </el-col>

        <el-col :span="6">
           <b>暴击:</b>

           <span :style="getSpecialAttrBonusStyle('crit')">
                <span v-if="getSpecialAttrBonus('crit') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('crit') }}
            </span>
        </el-col>

        <el-col :span="6">
            {{ getSkinAttrValue('crit') }}
        </el-col>

        <el-col :span="6">
            <b>幸运:</b>

            <span :style="getSpecialAttrBonusStyle('lucky')">
                <span v-if="getSpecialAttrBonus('lucky') > 0" style="color: #13CE66;">+</span>

                {{ getSpecialAttrBonus('lucky') }}
            </span>
        </el-col>

        <el-col :span="6">
            {{ getSkinAttrValue('lucky') }}
        </el-col>
    </el-row>
</template>

<script>

import SkinService from '../../../services/SkinService'

export default {
    props: ['skin', 'fontType'],

    methods: {
        getSkinAttrValue(attrName) {
            return SkinService.getSkinAttrValue(this.skin, attrName)
        },

        getAtkType(val) {
            return SkinService.getAtkType(val)
        },

        getSpecialAttrBonus(attrName) {
            return SkinService.getSpecialAttrBonus(this.skin, attrName)
        },

        displayCostJade(num) {
            return SkinService.displayCostJade(num)
        },

        getSpecialAttrBonusStyle(attrName) {
            let bonus = this.getSpecialAttrBonus(attrName)

            if (_.isEqual(0, Number(bonus))) {
                return {
                    display: 'none',
                }
            }

            let color = _.lt(0, bonus) ? '#13CE66' : '#FF4949';

            return _.isEqual(this.fontType, 'small') ? {
                color,
                float: 'right',
                'padding-right': '20px',
                'font-weight': 'bold',
            } : {
                color,
                float: 'right',
                'padding-right': '16px',
                'font-weight': 'bold',
            }
        },
    },

    computed: {
        getCostPercentage() {
            let totalCost = Number(this.skin.use_faith) + Number(this.skin.use_food)
            let percentage = Math.ceil(totalCost/30 * 100)

            return percentage
        },

        getCostPercentageStyle() {
            let val = this.getCostPercentage

            if (_.gt(33, val)) {
                return 'success'
            }

            if (_.gt(66, val)) {
                return ''
            }

            return 'exception'
        },

        getFontStyle() {
            if (_.isEqual(this.fontType, 'small')) {
                return {
                    'line-height': '21px',
                    'font-size': '14px',
                }
            }

            return {
                'line-height': '32px',
                'font-size': '19px',
            }
        },
    },

    mounted() {

    },
}
</script>

<style>

.champion-attr-info-border {
    margin-top: 15px;
    margin-bottom: 15px;
}

</style>


