import equipments from '../dataset/equipments'

let _ = require('lodash')

export default class EquipmentService
{
    static get weapons()
    {
        let weapons = []

        _.forEach(equipments, weapon => {
            if (_.isEqual(Number(weapon.type), 1)) {
                weapons.push(weapon)
            }
        })

        return weapons
    }

    static get armors()
    {
        let armors = []

        _.forEach(equipments, armor => {
            if (_.isEqual(Number(armor.type), 2)) {
                armors.push(armor)
            }
        })

        return armors
    }
}


